@forward 'globals';

input {
    margin-right: .5rem;
}

.users-container {
    background-color: #EEE;
    margin-top: 2rem;
    border-radius: 20px;
}

.user-profile {
    display: flex;
    align-items: center;
    gap: 1rem;
    padding: 1rem;
    margin-inline: 2rem;
    
    &:not(:last-of-type) {
        padding-bottom: 1rem;
        border-bottom: 1px solid #E6E6;
    }

    .delete-user {
        color: red;
        background-color: transparent;
        border-color: transparent;
        margin-left: auto;
        padding: 0;
        align-self: center;
        cursor: pointer;
    }

    .bio {
        margin-top: 1rem;
    }
}

.user-img {
    width: 5rem;
    height: 5rem;
    border-radius: 50%;
    overflow: hidden;

    img {
        object-fit: cover;
        display: block;
        aspect-ratio: 1;
        max-width: 100%;
    }
}

@media only screen and (max-width: 768px) {
    body {
        padding: 1rem;
    }

    .user-profile {
        display: block;

        .profile-info {
            margin-top: .5rem;
            margin-bottom: 2rem;
        }

        .delete-user {
            display: block;
            margin-left: auto;
        }
    }
}